.rfa-bim-container {
    max-width: 1500px;
    margin: 0 auto;
    padding: 20px;
    background: white;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Project Header Styles */
.project-header {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 20px;
}

.project-header select {
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
    min-width: 200px;
}

/* Search Box Styles */
.search-box {
    margin: 20px 0;
    text-align: right;
}

.search-box input {
    padding: 8px;
    width: 300px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

/* Table Styles */
.table-container {
    margin-bottom: 30px;
    overflow-x: auto;
}

.results-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
}

.results-table th,
.results-table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

.results-table th {
    background-color: #f5f5f5;
    font-weight: bold;
}

.results-table tr {
    cursor: pointer;
}

.results-table tr:hover {
    background-color: #f5f5f5;
}

/* Document Detail Container */
.document-detail-container {
    margin-top: 20px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 4px;
    background-color: #f9f9f9;
}

.document-detail-header {
    font-size: 1.2em;
    margin-bottom: 15px;
    padding-bottom: 10px;
    border-bottom: 1px solid #ddd;
}

.document-info {
    margin-bottom: 20px;
}

.document-info p {
    margin: 8px 0;
}

/* Form Styles */
.form-group {
    margin-bottom: 15px;
}

.form-group label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
}

.form-group input[type="text"],
.form-group input[type="file"] {
    width: 100%;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

/* Button Styles */
button {
    background: #4CAF50;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-weight: bold;
}

button:hover {
    background: #45a049;
}

button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
}

/* Message Styles */
.error-message {
    color: #dc3545;
    background-color: #ffebee;
    padding: 10px;
    margin: 10px 0;
    border-radius: 4px;
    text-align: center;
}

.no-documents-message {
    color: #dc3545;
    text-align: center;
    padding: 20px;
    font-weight: bold;
    margin-top: 20px;
}

/* Spinner Styles */
.spinner-container {
    text-align: center;
    margin-top: 15px;
}

.spinner {
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 3px solid rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    border-top-color: #4CAF50;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

/* Body Background */
body {
    min-height: 100vh;
}

/* Link Styles */
a {
    color: #2196F3;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

/* Responsive Styles */
@media screen and (max-width: 768px) {
    .rfa-bim-container {
        padding: 10px;
    }

    .search-box input {
        width: 100%;
    }

    .table-container {
        overflow-x: auto;
    }

    .results-table {
        font-size: 14px;
    }

    .document-detail-container {
        padding: 10px;
    }
}